.container {
  border: 5px solid black;
  top: 110px;
  position: relative;
  padding: 120px;
  box-shadow: 2px 2px 5px 20px rgb(0, 0, 0), 2px 2px 10px 35px rgb(255, 0, 221);
  border-radius: 50px;
}

body {
  height: 100vh;
  width: 100vw;
  background-size: cover;
  font-family: 'Arial Narrow Bold', sans-serif;
  background-repeat: no-repeat;
  background-position: center;
  animation-name: changebk;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: ease;

}

@keyframes changebk {
  from {
    background-image: linear-gradient(to bottom right, rgb(229, 255, 0), rgb(255, 2, 242), rgb(0, 255, 255), rgb(3, 223, 3));
  }

  to {
    background-image: linear-gradient(to bottom left, rgb(217, 255, 0), rgb(255, 4, 221), rgb(208, 255, 0), rgb(2, 247, 255));
  }
}

h1 {

  text-align: center;
  font-weight: bolder;
  font-family: Verdana;
  background: linear-gradient(50deg, rgb(0, 0, 0), rgb(0, 0, 0), rgb(255, 251, 2));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-repeat: no-repeat;
  background-size: 90%;
  animation: shine 1.7s linear infinite;
}

h2 {

  text-align: center;
  font-weight: bolder;
  font-family: Verdana;
  background: linear-gradient(50deg, rgb(0, 0, 0), rgb(0, 0, 0), rgb(255, 251, 2));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-repeat: no-repeat;
  background-size: 90%;
  animation: shine 1.7s linear infinite;
}

h5 {

  text-align: center;
  font-weight: bolder;
  font-size: 30px;
  font-family: Gill Sans MT;
  background: linear-gradient(50deg, rgb(0, 0, 0), rgb(0, 0, 0), rgb(255, 251, 2));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-repeat: no-repeat;
  background-size: 90%;
  animation: shine 1.7s linear infinite;

}

@keyframes shine {
  0% {
    background-position-x: -500%;
  }

  100% {
    background-position-x: 500%;
  }
}